<template>
  <!-- -->
  <div class="home">
<!--    <div style="width: 100%; height: 100%">-->
<!--      <el-backtop :bottom="60"></el-backtop>-->
<!--    </div>-->
    <!--  -->
    <heads ref="badgeChange"></heads>
    <div style="height: 60px; margin-bottom: 5px"></div>
    <!--  -->
    <div class="breadcrumb">
      <router-link :to="{ path: '/mine' }"
        ><span style="color: #00000075"></span></router-link
      >
      <span style="color: #00000073"> / </span>
      <span style="color: #000000a6"></span>
    </div>
    <!--  -->
    <div class="coupon">
      <coupon></coupon>
    </div>
    <!--  -->
    <bottoms></bottoms>
  </div>
</template>

<script>
import heads from "../components/head";
import bottoms from "../components/bottom";
import coupon from "../components/coupon";
export default {
  components: {
    heads,
    bottoms,
    coupon,
  },
  data() {
    return {
      i: "1",
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.badgeChange.getCount();
    });
    //  
    this.handClick();
  },
  methods: {
    handClick() {
      let oldTop = 0; //0
      // 
      window.onscroll = () => {
        let top = document.scrollingElement.scrollTop; //
        if(top > 0){
          this.$refs.badgeChange.fix = 1;
        }else{
          this.$refs.badgeChange.fix = 0;
        }
        //top
        if (oldTop >= top) {
          this.$nextTick(() => {
            this.$refs.badgeChange.header_ = true;
          });
        } else {
          //...
          this.$nextTick(() => {
            this.$refs.badgeChange.header_ = false;
          });
        }
        oldTop = top; //
      };
    },
  },
};
</script>

<style lang="scss">
.coupon {
  padding: 50px 23px;
  background-color: #fff;
  margin-bottom: 40px;
}
//   
.breadcrumb span {
  font-size: 14px;
}
.breadcrumb {
  height: 56px;
  line-height: 56px;
  border-bottom: 1px solid #eaeaea;
  padding: 0 16px;
  background-color: #fff;
}
</style>

