/**
 * getget
 */
export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params,
      })
      .then((res) => {
        switch (res.data.code) {
          case 0:
            resolve(res.data);
            break;
          case -1:
            this.$message.error("");
          case 500:
            this.$message.error("");
            break;
          case 404:
            this.$message.error("");
            break;
          case 403:
            this.$message.error("");
            break;
          case 401:
            Message.error("");
            sessionStorage.removeItem("token");
            router.replace("/mine");
            break;
          case 405:
            this.$message.error("");
            break;
          case 406:
            this.$message.error("");
            break;
        }
      })
      .catch((err) => {
        reject(err.data);
        // this.$message({
        //   showClose: true,
        //   message: "",
        //   type: "error",
        // });
      });
  });
}
