import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    //
    isAuthenticate: false,
    user: null,
    pay: null,
    openid: "",
    wxPay: "", //
    heji: "", //
    setShoppingBag:[] // 
  },
  //()--
  mutations: {
    setIsAuthenticate(state, data) {
      state.isAuthenticate = data;
    },
    setUser(state, data) {
      state.user = data;
    },
    setHeJi(state, data) {
      state.heji = data;
    },
    setPay(state, data) {
      state.pay = data;
    },
    setOpenID(state, data) {
      state.openid = data;
    },
    setWxPay(state, data) {
      state.wxPay = data;
    },
    setShoppingBag(state,data) {
      state.setShoppingBag = data;
    }
  },
  //()--
  actions: {
    setIsAuthenticate({ commit }, data) {
      commit("setIsAuthenticate", data);
    },
    setUser({ commit }, data) {
      commit("setUser", data);
    },
    setHeJi({ commit }, data) {
      commit("setHeJi", data);
    },
    setPay({ commit }, data) {
      commit("setPay", data);
    },
    setOpenID({ commit }, data) {
      commit("setOpenID", data);
    },
    setWxPay({ commit }, data) {
      commit("setWxPay", data);
    },
    setShoppingBag({ commit }, data) {
      commit("setShoppingBag", data);
    },
   
  },
  // /--
  getters: {
    getIsAuthenticate(state) {
      return state.isAuthenticate;
    },
    getUser(state) {
      return state.user;
    },
    getHeJi(state) {
      return state.heji;
    },
    getPay(state) {
      return state.pay;
    },
    getOpenID(state) {
      return state.openid;
    },
    getWxPay(state) {
      return state.wxPay;
    },
    setShoppingBag(state) {
      return state.setShoppingBag;
    }
    
  },
  modules: {},
});
