<template>
  <div class="city-select">
    <select class="text-indent" v-model="selectedProvince" name="province">
      <option
        v-for="(item, index) in provinces"
        v-if="item.level === 1"
        :value="item"
      >
        {{ item.name }}
      </option>
    </select>
    <select class="text-indent" v-model="selectedCity" name="city">
      <option v-for="(item, index) in cities" :value="item">
        {{ item.name }}
      </option>
    </select>
    <select class="text-indent" v-model="selectedBlock" name="block">
      <option v-for="(item, index) in blocks" :value="item">
        {{ item.name }}
      </option>
    </select>
  </div>
</template>
<script>
/**
 *    /  
 * code: 
 * sheng: 
 * name: 
 * level: , level = 1,  level=2, / level = 3
 * di: ,
 */
import provinces from "../components/js/provinces";
import Vue from "vue";
export default {
  name: "app",
  created() {
    // ,,;18
    let beijing = this.provinces.slice(0, 19);
    this.cities = beijing.filter((item) => {
      if (item.level === 2) {
        return true;
      }
    });
    this.selectedCity = this.cities[0];
    this.blocks = beijing.filter((item) => {
      if (item.level === 3) {
        return true;
      }
    });
    this.selectedBlock = this.blocks[0];
  },
  watch: {
    selectedProvince(newVal, oldVal) {
      // ,
      if (
        newVal.sheng === "71" ||
        newVal.sheng === "81" ||
        newVal.sheng === "82"
      ) {
        this.cities = [newVal];
        this.blocks = [newVal];
      } else {
        this.cities = this.provinces.filter((item) => {
          if (item.level === 2 && item.sheng && newVal.sheng === item.sheng) {
            return true;
          }
        });
      }
      // DOM,
      Vue.nextTick(() => {
        this.selectedCity = this.cities[0];
        this.$emit("input", this.info);
      });
    },
    selectedBlock() {
      Vue.nextTick(() => {
        this.$emit("input", this.info);
      });
    },
    selectedCity(newVal) {
      // , di,sheng
      if (
        newVal.sheng === "71" ||
        newVal.sheng === "81" ||
        newVal.sheng === "82"
      ) {
        this.blocks = [newVal];
        this.cities = [newVal];
      } else {
        this.blocks = this.provinces.filter((item) => {
          if (
            item.level === 3 &&
            item.sheng &&
            item.sheng == newVal.sheng &&
            item.di === newVal.di &&
            item.name !== ""
          ) {
            return true;
          }
        });
      }
      Vue.nextTick(() => {
        this.selectedBlock = this.blocks[0];
        //  v-model input
        // console.log(this.info.block.name);
        this.$emit("input", this.info);
      });
    },
  },
  computed: {
    info() {
      return {
        province: this.selectedProvince,
        city: this.selectedCity,
        block: this.selectedBlock,
      };
    },
  },
  data() {
    return {
      selectedProvince: provinces[0],
      selectedCity: 0,
      selectedBlock: 0,
      cities: 0,
      provinces,
      blocks: 0,
    };
  },
};
</script>
<style lang="scss" scoped>
.city-select select {
  outline: 0;
  height: 40px;
  border: 1px solid #e8e8e8;
  background-color: #fff;
  width: 100px;
}
.city-select select:hover {
  border: 1px solid #f56c6c;
}
.text-indent {
  text-indent: 8px;
}
</style>
