<template>
  <!--  -->
  <div>
<!--    <div style="width: 100%; height: 100%">-->
<!--      <el-backtop :bottom="20"></el-backtop>-->
<!--    </div>-->
    <!--  -->
    <heads ref="badgeChange"></heads>
    <!-- Skeleton  -->
    <div class="skeleton" v-show="skeletonShow">
      <van-skeleton :row="1" class="van" />
    </div>

    <transition-group name="hehe">
      <div
        v-for="(item, index) in productlist"
        :key="index"
        style="position: relative"
        v-show="FadeOutFrame"
      >
        <!--  -->
        <div class="bg">
          <el-carousel
            height="375px"
            :autoplay="false"
            arrow="never"
            class="cs-list"
            ref="carousel"
            indicator-position="none"
          >
            <el-carousel-item
              v-for="(items, indexs) in item.img_list"
              :key="indexs"
              :name="index.toString() + indexs.toString()"
            >
              <img
                :src="items[0]"
                width="100%"
                height="100%"
                @click="router_img(item)"
              />
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="details-flex">
          <div class="title" style="font-family: 'Avenir Next', Avenir, 'Segoe UI', Roboto, -apple-system, BlinkMacSystemFont, sans-serif">
            <p>{{ item.title }}</p>
            <p
              style="
                color: rgba(0, 0, 0, 0.45);
                margin-top: 5px;
                font-size:14px
              "
            >
              {{ item.price }}
            </p>
          </div>

          <div class="flex">
            <!--  -->
            <div
              v-for="(c, row) in item.color_list"
              @click="ChangeImage(index, row)"
              style="margin-left: 10px"
              :key="row"
            >
              <div class="colors" :class="row == att ? 'color_border_1' : 'color_border_2'" :style="{ background: c }"></div>
            </div>
          </div>
        </div>
      </div>
    </transition-group>
  </div>
</template>

<script>
import heads from "../components/head";
export default {
  components: {
    heads,
  },
  data() {
    return {
      img_list: [],
      productlist: [],
      number: 0,
      i: "1",
      att: 0,
      skeletonShow: true,
      isFirstEnter: false, // false
      FadeOutFrame: true,
    };
  },
  watch: {
    $route() {
      if (this.$route.path == "/productList" && this.$route.query.id) {
        var newid = this.$route.query.id; //id
        var oldid = sessionStorage.getItem("oldlistid"); //id

        if (oldid != newid) {
          sessionStorage.setItem("oldlistid", this.$route.query.id);
          this.listid = this.$route.query.id;
          this.getCase();
        }
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    if (from.name == "shopdetails") {
      to.meta.isBack = true;

      //
      //shopdetails
    }
    next();
  },
  created() {
    this.isFirstEnter = true;
    // 
    // keep-alive2+
  },
  mounted() {
    // this.getCase();
    this.share()
  },
  activated() {
    this.handClick();
    this.$nextTick(() => {
      this.$refs.badgeChange.getCount();
    });
    if (!this.$route.meta.isBack || this.isFirstEnter) {
      // isBackfalse
      // isFirstEntertrue
      this.productlist = []; // 
      this.img_list = []; // 
      this.getCase();
    }
    if (this.$route.query.id) {
      this.listid = this.$route.query.id;
      sessionStorage.setItem("oldlistid", this.$route.query.id);
    }
    // falseisBacktrue
    this.$route.meta.isBack = false;
    // falseisBacktrue
    this.isFirstEnter = false;
  },
  methods: {
    share() {
      var _this = this
      this.$get(this.api.config, {
        url: window.location.href,
      }).then((res) => {
        if (res.code !== 0) return;
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: "wx1ca48ea15878c6c7", // 
          timestamp: res.data.timestamp, // 
          nonceStr: res.data.nonceStr, // 
          signature: res.data.sign, // 
          jsApiList: ["onMenuShareAppMessage"], // JS
        });
        wx.ready(function () {
          wx.onMenuShareAppMessage({
            title: "KOKOU ", // 
            desc: '', // 
            imgUrl: "http://image.kokou.cn//20210322/PUL9EZ3HK.jpg", // 
            success: function () {
              // 
            },
            cancel: function () {
              // 
            },
          });
        });
      });
    },
    handClick() {
      let oldTop = 0; //0
      // 
      window.onscroll = () => {
        let top = document.scrollingElement.scrollTop; //
        if(top > 0){
          this.$refs.badgeChange.fix = 1;
        }else{
          this.$refs.badgeChange.fix = 0;
        }
        //top
        if (oldTop >= top) {
          this.$nextTick(() => {
            this.$refs.badgeChange.header_ = true;
          });
        } else {
          //...
          this.$nextTick(() => {
            this.$refs.badgeChange.header_ = false;
          });
        }
        oldTop = top; //
      };
    },
    router_img(item) {
      console.log(item);
      window.localStorage.setItem("inventory", JSON.stringify(item.inventory));
      window.localStorage.setItem("img_list", JSON.stringify(item.img_list));
      window.localStorage.setItem(
        "color_list",
        JSON.stringify(item.color_list)
      );
      window.localStorage.setItem("code_list", JSON.stringify(item.code_list));
      this.$router.push({
        path: "/shopdetails",
        query: {
          id: item.id,
          price: item.price,
          title: item.title,
          code_list: item.code_list,
          desc: item.desc,
        },
      });
    },
    getCase() {
      this.$get(this.api.case, {
        case_id: this.$route.query.id,
      })
        .then((res) => {
          this.$nextTick(() => {
            this.$refs.badgeChange.getCount();
          });
          this.productlist = JSON.parse(JSON.stringify(res.data));
          this.productlist.map(item => {
            item.price = item.price.substring(0, item.price.indexOf('.'))
          })
          for (let i = 0; i < this.productlist.length; i++) {
            if (this.productlist[i].recommend == "0") {
              this.productlist.splice(i, 1);
            }
          }
          if (this.productlist.length !== 0) {
            this.skeletonShow = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },

    ChangeImage(index, row) {
      this.att = row;
      this.number = index; //
      this.$refs.carousel[this.number].setActiveItem(
        index.toString() + row.toString()
      ); //row
      // index.toString()+row.toString()
      // nn
    },
  },
};
</script>

<style lang="scss" scoped>
.color_border_2 {
  box-shadow: 0 0 0 1px #f8f8f8;
}
.color_border_1 {
  box-shadow: 0 0 0 3px #cccccc;
}
.hehe-enter {
  opacity: 0;
}
.hehe-enter-to {
  opacity: 1;
}
.hehe-enter-active {
  transition: all 1s;
}

.van /deep/ .van-skeleton__row {
  height: 100%;
  width: 100% !important;
}
.skeleton /deep/ .van-skeleton {
  padding: 0;
}
.flex {
  display: flex;
}
.title p {
  font-size: 16px;
  color: #111;
}
.details-flex {
  height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 10px;
}
.colors {
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
</style>
