<template>
  <!-- -->
  <div class="home">
<!--    <div style="width: 100%; height: 100%">-->
<!--      <el-backtop :bottom="20"></el-backtop>-->
<!--    </div>-->
    <!--  -->
    <heads ref="badgeChange"></heads>
    <!-- Skeleton  -->
    <div class="skeleton" v-show="skeletonShow">
      <van-skeleton :row="1" />
    </div>
    <!--  -->
    <el-carousel
      height="462px"
      class="cs-list"
      indicator-position="none"
      arrow="never"
    >
      <el-carousel-item v-for="item in carousel" :key="item.id">
        <img :src="item.img" />
        <!-- <h3>{{ item.title }}</h3> -->
        <!-- <h5>{{item.name}}</h5> -->
      </el-carousel-item>
    </el-carousel>
    <!--  -->
    <div v-for="item in content" :key="item.id" class="content-list">
      <img :src="item.img" @click="homeImg(item)" />
      <!-- <h3>{{ item.title }}</h3>
      <h5>{{ item.info }}</h5> -->
    </div>
    <!--  -->
    <bottoms></bottoms>
  </div>
</template>

<script>
import heads from "../components/head";
import bottoms from "../components/bottom";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import { log } from "util";
import { opendirSync } from "fs";
export default {
  components: {
    heads,
    bottoms,
  },
  data() {
    return {
      carousel: [], //
      content: [], //
      i: "1",
      skeletonShow: true,
      isFirstEnter: false, // false
      // getOpenID:''
    };
  },
  computed: {
    ...mapGetters(["getIsAuthenticate", "getUser", "getOpenID"]),
  },
  beforeRouteEnter(to, from, next) {
    if (from.name == "productList") {
      to.meta.isBack = true;
      //
      //ProductList,shopdetails
    }

    next();
  },
  created() {
    this.isFirstEnter = true;
    // 
    // keep-alive2+
  },
  mounted() {
    //  
    this.getHome();
    this.share()
  },
  activated() {
    this.handClick();
    this.$nextTick(() => {
      this.$refs.badgeChange.getCount();
    });
    if (!this.$route.meta.isBack || this.isFirstEnter) {
      // isBackfalse
      // isFirstEntertrue
      this.carousel = []; // 
      this.content = []; // 
      this.getHome();
    }
    // falseisBacktrue
    this.$route.meta.isBack = false;
    // falseisBacktrue
    this.isFirstEnter = false;
  },
  methods: {
    share() {
      var _this = this
      this.$get(this.api.config, {
        url: window.location.href,
      }).then((res) => {
        if (res.code !== 0) return;
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: "wx1ca48ea15878c6c7", // 
          timestamp: res.data.timestamp, // 
          nonceStr: res.data.nonceStr, // 
          signature: res.data.sign, // 
          jsApiList: ["onMenuShareAppMessage"], // JS
        });
        wx.ready(function () {
          wx.onMenuShareAppMessage({
            title: "KOKOU ", // 
            desc: '', // 
            imgUrl: "http://image.kokou.cn//20210322/PUL9EZ3HK.jpg", // 
            success: function () {
              // 
            },
            cancel: function () {
              // 
            },
          });
        });
      });
    },
    handClick() {
      let oldTop = 0; //0
      // 
      window.onscroll = () => {
        let top = document.scrollingElement.scrollTop; //
        if(top > 0){
          this.$refs.badgeChange.fix = 1;
        }else{
          this.$refs.badgeChange.fix = 0;
        }

        //top
        if (oldTop >= top) {
          // console.log(top);
          this.$nextTick(() => {
            this.$refs.badgeChange.header_ = true;
          });
        } else if (top > 50) {
          console.log(top);
          //...
          this.$nextTick(() => {
            this.$refs.badgeChange.header_ = false;
          });
        }
        oldTop = top; //
      };
    },
    getHome() {
      this.$get(this.api.home)
        .then((res) => {
          this.$nextTick(() => {
            this.$refs.badgeChange.getCount();
          });
          if (res.data !== "") {
            this.skeletonShow = false;
          }
          this.carousel = res.data.carousel;
          this.navigation = res.data.navigation;
          this.content = res.data.content;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    homeImg(item) {
      console.log(item);
      // window.localStorage.setItem("inventory", JSON.stringify(item.inventory));
      // window.localStorage.setItem("img_list", JSON.stringify(item.img_list));
      // window.localStorage.setItem(
      //   "color_list",
      //   JSON.stringify(item.color_list)
      // );
      // window.localStorage.setItem("code_list", JSON.stringify(item.code_list));
      this.$router.push({
        path: "/productList",
        query: {
          id: item.goods_id,
          // price: item.price,
          // title: item.title,
          // code_list: item.code_list,
          // desc: item.desc,
        },
      });
    },
  },
  unmounted() {
    // window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

<style lang="scss" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  height: 0;
}

.skeleton /deep/ .van-skeleton {
  height: 800px;
  padding: 0;
}
// 
.content-list h5 {
  font-size: 16px;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  color: #52525280;
}
.content-list h3 {
  font-size: 18px;
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  color: #111;
}
.content-list img {
  width: 100%;
  height: 100%;
}
.content-list {
  height: 300px;
  position: relative;
}
// 
.cs-list /deep/ .el-carousel__button {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #333;
}
.cs-list h5 {
  font-size: 16px;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.cs-list h3 {
  font-size: 20px;
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
}
.cs-list img {
  width: 100%;
  height: 100%;
}
.cs-list {
  position: relative;
  // margin-bottom: 10px;
}
</style>

